.bg-img {
    width: 100%;
    padding-top: 10px;
    min-height: 100%;
    float: center;
    background: url(https://www.reg.cmu.ac.th/webreg/reg-img/reg_header_bg.png) right bottom no-repeat !important;
}
.bg-img img {
    width: 250px;
}

/* navbar */
.navbar-toggler:focus {
    text-decoration: none !important;
    outline: 0 !important;
    box-shadow: none !important;

}

.main-navigation {
    z-index: 1;
    border-bottom: 1px solid #9a76b2;
    box-shadow: 0 0 10px rgba(0,0,0,.15), 0 4px 4px rgba(0,0,0,.15);
    background: #674392;
}
.nav-link {
    color: #ffffff;
    font-size: 18px;
    padding: 10px;
    margin-right: 10px;
    border-right: 1px solid rgba(255, 255, 255, 0.4);
    border-left: 1px solid rgba(255, 255, 255, 0.4);
   
}
.nav-link:hover {
    color: #e7e7e7;
    
}


